import React from "react";
import './App.css';

function Case({ image, company, desc, article}) {
    return (
        <div className="caseContainer">
            <div>
                <img alt="" src={image}/>
            </div>
            <div className="caseDescription">
                <h1>{company}</h1>
                <p>{desc}</p>
                {article.length > 0 && <a target="_blank" rel="noopener noreferrer" href={article}>Read more</a>}
            </div>
        </div>
    )
}

export default Case;



