import React from "react";
import './App.css';

function LinkRow(props) {
    return(
        <div className="row">
            <h1>{props.title}</h1>
            <p className="social"><a target="_blank" rel="noopener noreferrer" href={props.url}>{props.date}</a></p>
        </div>
    )
}

export default LinkRow;


