import React from "react";
import Row from './Row.js'
import LinkRow from './LinkRow.js'
import Title from './Title.js'
import Case from './Case.js'
import './App.css';
import data from './data/data.json'

// Get data from JSON
const caseData = data.map((data) => {
    return(
        <Case key={data.id} company={data.case} desc={data.description} image={data.primaryImage} imgSec={data.secondaryImage} article={data.article}/>
    )
})

function About(props) {
    return(
        <div className="intro">
            <p>I’m Farbod — a product designer and every so often a developer. Currently working on the holistic mobile experience at <a href="http://spotify.design">Spotify</a>.</p>
            <p className="smiley--small">☻</p>
            <section className="contact">
                <div><p className="name">Farbod Faramarzi</p></div>
            </section>
            <section className="work">
                <Title title="Journey"/>
                <Row title="Spotify" date="May 2020 — now" desc="Working on the holistic user experience cross consumer-based apps and design systems for mobile platforms." />
                <Row title="Spotify" date="Jun 2017 — May 2020" desc="Designing for the Desktop App and Web Player. Designing a new experience for the Tablet App." />
                <Row title="Spotify" date="May 2017 — Jun 2017" desc="Designing for big screens, game consoles, speakers, and smart appliances." />
                <Row title="Viaplay" date="Oct 2014 — May 2017" desc="Designing for big screens, game consoles, and iOS devices." />
            </section>
            
            <section className="links">
                <Title title="selected work"/>
                {/*Add Data*/}
                {caseData}
            </section>

            <section className="links">
                <Title title="Find me elsewhere"/>
                <LinkRow url="http://www.twitter.com/fabbylicious" title="Twitter" date="@fabbylicious"/>
                <LinkRow url="mailto:hello@fabbe.lol" title="Email" date="hello@fabbe.lol"/>
                <LinkRow url="https://www.linkedin.com/in/farbodfaramarzi/" title="LinkedIn" date="Farbod Faramarzi"/>
                <LinkRow url="https://www.read.cv/fabbe" title="Read.cv" date="read.cv/fabbe"/>
            </section>
        </div>
    )
}

export default About;


