import React from 'react';
import { HashRouter, Route } from "react-router-dom";
import About from './About.js'; 
import './App.css';


function App() {
  return (
    <HashRouter basename="/">
      <header></header>
      <main>
        <Route exact path="/" component={About}/>
      </main>
    </HashRouter>
  );
}

export default App;



